/* Login Page Styling */
.wrapper {
    max-width: 450px;
    width: 100%;
    background-color: #ffffff; /* White background */
    border-radius: 12px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1); /* Soft shadow */
    padding: 30px;
    margin: 20px auto;
    transition: transform 0.3s ease-in-out;
  }
  
  .wrapper:hover {
    transform: translateY(-5px); /* Slight hover effect */
  }
  
  h1 {
    font-size: 28px;
    font-weight: 600;
    color: #2d3748; /* Dark gray */
    text-align: center;
    margin-bottom: 20px;
  }
  
  .input-box {
    position: relative;
    margin-bottom: 20px;
  }
  
  .input-box input {
    width: 85%;
    padding: 14px 20px;
    padding-right: 50px; /* Space for icon */
    border: 1px solid #cbd5e0; /* Light border */
    border-radius: 8px;
    font-size: 15px;
    color: #4a5568; /* Medium gray */
    background-color: #f7fafc; /* Light input background */
    transition: border-color 0.3s ease-in-out;
  }
  
  .input-box input:focus {
    border-color: #3182ce; /* Blue border on focus */
    outline: none;
  }
  
  .input-box .icon {
    position: absolute;
    right: 15px;
    top: 50%;
    transform: translateY(-50%);
    font-size: 18px;
    color: #718096; /* Gray */
  }
  
  .button {
    width: 100%;
    padding: 12px;
    border: none;
    border-radius: 8px;
    background-color: #3182ce; /* Primary blue */
    color: #ffffff;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .button:hover {
    background-color: #2b6cb0; /* Darker blue */
  }
  
  .register-link {
    text-align: center;
    margin-top: 15px;
    font-size: 14px;
  }
  
  .register-link a {
    color: #3182ce;
    text-decoration: none;
    font-weight: 600;
  }
  
  .register-link a:hover {
    text-decoration: underline;
  }
  
  .message {
    text-align: center;
    font-size: 14px;
    margin-bottom: 15px;
  }
  
  .message.error {
    color: #e53e3e; /* Red for error messages */
  }
  
  .message.success {
    color: #38a169; /* Green for success messages */
  }
  
  input::placeholder {
    color: #a0aec0; /* Light gray placeholder */
  }
  
  input:disabled {
    background-color: #e2e8f0;
    color: #a0aec0;
    cursor: not-allowed;
  }
  