/* Container styling */
.items-container {
    padding: 20px;
    text-align: center;
  }
  
  /* Product list grid layout */
  .items-list {
    display: grid;
    grid-template-columns: repeat(4, minmax(220px, 1fr)); /* Consistent item size */
    gap: 20px;
    justify-content: center;
  }
  
  /* Styling for each item card */
  .item-card {
    border: 1px solid #ddd;
    border-radius: 12px;
    padding: 15px;
    background-color: #ffffff;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    transition: transform 0.2s, box-shadow 0.2s;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative; /* Ensure like icon positions correctly */
  }
  
  .item-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 15px rgba(0, 0, 0, 0.2);
  }
  
  /* Image styling */
  .item-image {
    width: 150px;
    max-width: 180px; /* Ensures uniform image size */
    height: 150px; /* Fixed height for uniformity */
    object-fit: cover; /* Maintain aspect ratio */
    border-radius: 8px;
    margin-bottom: 10px;
  }
  
  /* Product title styling */
  .item-card h3 {
    font-size: 1.2rem;
    margin: 10px 0;
  }
  
  /* Button styling */
  button {
    margin: 5px 0;
    padding: 8px 15px;
    border: none;
    border-radius: 6px;
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.3s, box-shadow 0.2s;
  }
  
  .like-button {
    background-color: #ff4d4d;
    color: white;
  }
  
  .like-button:hover {
    background-color: #e84343;
  }
  
  .buy-button {
    background-color: #4caf50;
    color: white;
  }
  
  .buy-button:hover {
    background-color: #45a045;
  }
  
  /* Quantity control buttons */
  .quantity-controls {
    display: flex;
    gap: 10px;
    justify-content: center;
    width: 100%;
    margin-top: 8px;
  }
  
  /* Icon styling */
  .like-icon {
    position: absolute;
    top: 10px;
    right: 10px;
    color: gray;
    border: none;
    background: none;
    cursor: pointer;
    font-size: 1.5rem;
    transition: color 0.3s;
  }
  
  .like-icon.active {
    color: red;
  }
  
  .like-icon:hover {
    color: #e84343;
  }
  
  /* Button hover effect */
  button:hover {
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  }
  
  /* Responsive design for smaller screens */
  @media (max-width: 768px) {
    .items-list {
      grid-template-columns: repeat(2, minmax(200px, 1fr)); /* Two items per row */
    }
  }
  
  @media (max-width: 480px) {
    .items-list {
      grid-template-columns: 1fr; /* One item per row */
    }
  
    .item-card {
      max-width: 100%;
    }
  
    .like-icon {
      font-size: 1.2rem;
    }
  }
  